import { Directive, ElementRef, Renderer, HostListener, HostBinding} from '@angular/core';

@Directive({
  selector: '[bcchanger]'
})
export class BcchangerDirective {

  @HostListener('keyup', ['$event'])
    inputChanged(event) {
// console.log(event.target.value, isNaN(event.target.value));
      let classList: string[] = Array.from(event.target.classList) as Array<string>;
      let cv: boolean = this.isValid(classList);
      if (cv) {
        if (event.target.value) {
          if(event.target.value >= 0 && event.target.value < 10) {
            this.backgroundColor = '#5cb85c';
          } else if (event.target.value >= 10 && event.target.value < 20) {
            this.backgroundColor = '#337ab7';
          } else {
            this.backgroundColor = 'yellow';
          }
        } else {
          this.backgroundColor = 'red';
        }
      } else {
        this.backgroundColor = 'red';
      }
    }

  @HostBinding('style.backgroundColor') get setColor() {
      return this.backgroundColor;
    };

  private backgroundColor: string = 'white';

  constructor() {}

  isValid(classList: string[]){
    if(classList.indexOf('ng-invalid') < 0) {
      return true;
    } else {
      return false;
    }
  }

}



