import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Rx';
import {
FormArray,
FormGroup,
FormControl,
Validators,
FormBuilder
} from '@angular/forms';

import { RecipeService } from '../recipe.service';
import { Recipe } from '../recipe';

@Component({
  selector: 'rb-recipe-edit',
  template: require('./recipe-edit.component.html'),
  styles: [`
    #descAddon, #ingAddon {
      border-right: 1px solid #ccc;
      border-top-right-radius: 4px;
      border-bottom-left-radius: 0;
      border-bottom: 0;
    }

    #descRow { margin-top: 15px; }

    #ingListDiv {
      padding-left: 15px;
      border: 1px solid #ccc;
      border-bottom-right-radius: 4px;
      border-bottom-left-radius: 4px;
    }

    #content, #ingListDiv {
      border-top-right-radius: 0px;
      border-top-left-radius: 0px;
      margin-top: 0px;
      margin-bottom: 0px;
    }

    #newIngButton {}

    textarea.ng-invalid, input.ng-invalid {
      background-color: red;
    }
  `]
})
export class RecipeEditComponent implements OnInit, OnDestroy {
  recipeForm: FormGroup;
  addIngForm: FormGroup;
  private recipeIndex: number;
  private recipe: Recipe;
  private isNew = true;
  private subscription: Subscription;

  constructor(private route: ActivatedRoute,
              private recipeService: RecipeService,
              private formBuilder: FormBuilder,
              private router: Router) {}

  ngOnInit() {
    this.subscription = this.route.params.subscribe(
      (params: any) => {
        if (params.hasOwnProperty('id')) {
          this.isNew = false;
          this.recipeIndex = +params['id'];
          this.recipe = this.recipeService.getRecipe(this.recipeIndex);
        } else {
          this.isNew = true;
          this.recipe = null;
        }
        this.initForm();
      }
    );
  }

  onSubmit() {
    const newRecipe:Recipe = this.recipeForm.value;
    if (this.isNew) {
      this.recipeService.addRecipe(newRecipe);
    } else {
      this.recipeService.editRecipe(this.recipe, newRecipe);
    }
    this.navigateBack();
  }

  private navigateBack() {
    if (this.isNew) {
      this.router.navigate(['../']);
    } else {
      this.router.navigate(['/recipes', this.recipeIndex]);
    }
  }

  onCancel() {
    this.navigateBack();
  }

  onAddItem(name: string, amount: string) {
    (<FormArray>this.recipeForm.controls['ingredients']).push(
      new FormGroup({
        name: new FormControl(name, Validators.required),
        amount: new FormControl((+amount), [
          Validators.required,
          Validators.pattern("\\d+")
        ])
      })
    );
  }

  onRemoveItem(index: number) {
    (<FormArray>this.recipeForm.controls['ingredients']).removeAt(index);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private initForm() {
    let recipeName = '';
    let recipeImageUrl = '';
    let recipeContent = '';
    let recipeIngredients: FormArray = new FormArray([]);

    if (!this.isNew) {
      if (this.recipe.hasOwnProperty('ingredients')) {
        for (let i = 0; i < this.recipe.ingredients.length; i++) {
          recipeIngredients.push(
            new FormGroup({
              name: new FormControl(this.recipe.ingredients[i].name, Validators.required),
              amount: new FormControl((+this.recipe.ingredients[i].amount), [
                Validators.required,
                Validators.pattern("\\d+")
              ])
            })
          );
        }
      }
      recipeName = this.recipe.name;
      recipeImageUrl = this.recipe.imagePath;
      recipeContent = this.recipe.description;
    }
    this.recipeForm = this.formBuilder.group({
      name: [recipeName, Validators.required],
      imagePath: [recipeImageUrl, Validators.required],
      description: [recipeContent, Validators.required],
      ingredients: recipeIngredients
    });

    this.addIngForm = this.formBuilder.group({
      name: new FormControl('', Validators.required),
      amount: new FormControl(1, [
        Validators.required,
        Validators.pattern("\\d+")
      ])
    });
  }

}



