import { Component, OnInit } from '@angular/core';

import { RecipeService } from './recipe.service';
import { Recipe } from './recipe';

@Component({
  selector: 'rb-recipe-start',
  template: require('./recipe-start.component.html'),
  styles: []
})
export class RecipeStartComponent implements OnInit {

  public myInterval:number = 4000;
  public noWrapSlides:boolean = false;
  public slides:Array<any> = [];

  constructor(private recipeService: RecipeService) {
  }

  ngOnInit() {
    this.setSlides(this.recipeService.getRecipes());
    this.recipeService.recipesChanged.subscribe(
      (recipes: Recipe[]) => {
        this.setSlides(recipes);
      }
    );
  }

  setSlides(recipes: Recipe[]) {
    this.slides = [];
    for (let i=0 ; i< recipes.length ; i++){
      if (i>9) {
        break;
      }
      this.slides.push({
        image: recipes[i].imagePath,
        text: recipes[i].name
      });
    }
  }

}



